import { useEffect } from "react";
import { Link } from "react-router-dom";
const App = () => {
  useEffect(() => {
    document.title = "404 | YM";
  });
  return (
    <>
      <div
        style={{
          fontFamily: "Roboto Slab",
          fontSize: "150px",
          color: "#555",
          textAlign: "center",
        }}
      >
        404
      </div>
      <br />
      <div
        style={{
          fontFamily: "Roboto Slab",
          fontSize: "40px",
          color: "#555",
          textAlign: "center",
        }}
      >
        Page Not Found
      </div>
      <br />
      <br />
      <div
        style={{
          fontSize: "24px",
          color: "#555",
          textAlign: "center",
        }}
      >
        Return to{" "}
        <Link to="/" style={{ textDecoration: "underline" }}>
          Home Page
        </Link>
        .
      </div>
    </>
  );
};

export default App;
