import { useState, useEffect } from "react";
import ReactDom from "react-dom";
import { HashRouter, Switch, Route } from "react-router-dom";

import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faTools } from "@fortawesome/free-solid-svg-icons";

import Nav from "./navbar";
import Foot from "./footer";
import Home from "./home";
import About from "./about";
import Donate from "./donate";
import Contact from "./contact";
import Error from "./error";

import "./style.css";

var App = () => {
  var [device, setDevice] = useState("compatible");
  var func = () => {
    if (window.innerWidth < 1000) {
      setDevice("uncompatible");
    } else {
      setDevice("compatible");
    }
  };
  useEffect(() => {
    window.addEventListener("resize", func);
    func();
    return () => {
      window.removeEventListener("resize", func);
    };
  }, []);
  if (device === "uncompatible")
    return (
      <>
        <div
          style={{
            fontStyle: "Roboto Slab",
            color: "#555",
            fontSize: "25px",
            padding: "100px 20px",
            textAlign: "center",
          }}
        >
          Mobile website under maintenance. Try to run on bigger screen.
          <br />
          <p style={{ fontSize: "100px", color: "#999" }}>
            <FontAwesomeIcon icon={faTools} />
          </p>
        </div>
      </>
    );
  return (
    <HashRouter>
      <Switch>
        <Route path="/" exact>
          <Nav />
          <div className="content">
            <Home />
          </div>
          <Foot />
        </Route>
        <Route path="/about" exact>
          <Nav />
          <div className="content">
            <About />
          </div>
          <Foot />
        </Route>
        <Route path="/donate" exact>
          <Nav />
          <div className="content">
            <Donate />
          </div>
          <Foot />
        </Route>
        <Route path="/contact" exact>
          <Nav />
          <div className="content">
            <Contact />
          </div>
          <Foot />
        </Route>
        <Route>
          <Error />
        </Route>
      </Switch>
    </HashRouter>
  );
};

ReactDom.render(<App />, document.getElementById("root"));
