import { useEffect } from "react";
import backImg from "./assets/img/back1.jpg";

const App = () => {
  useEffect(() => {
    document.title = "Home | YM";
  });
  useEffect(() => {
    var d = document;
    var x = !d.getElementById("razorpay-embed-btn-js");
    if (x) {
      var s = d.createElement("script");
      s.defer = !0;
      s.id = "razorpay-embed-btn-js";
      s.src = "https://cdn.razorpay.com/static/embed_btn/bundle.js";
      d.body.appendChild(s);
    } else {
      var rzp = window["__rzp__"];
      rzp && rzp.init && rzp.init();
    }
  });
  return (
    <>
      <div className="back-img">
        <img src={backImg} />
        <div className="back-img-text head">YASH MITTAL</div>
        <div className="back-img-text desc">IIT ROPAR, INDIA</div>
        <div className="back-img-text desc-2">
          GRIP TASK #3
          <br />
          PAYMENT GATEWAY INTEGRATION
        </div>
      </div>
      <div style={{ padding: "20px 100px", width: "100%" }}>
        <div style={{ fontSize: "30px" }}>A NON-PROFIT ORGANISATION</div>
        <div style={{ fontSize: "20px" }}>
          <p>
            Lorem, ipsum dolor sit amet consectetur adipisicing elit. Fugit
            dolorem minima aliquam fuga reiciendis voluptas odio provident ipsam
            quasi ad ullam, quo consequuntur ut tenetur. Totam doloremque ut
            incidunt. Voluptas?
          </p>
          <p>
            Expedita modi culpa doloremque eveniet, commodi ab saepe similique
            laudantium dignissimos delectus. Eaque esse ab ex amet adipisci,
            cupiditate ad, qui dolore odit sequi dolorum similique temporibus
            eius! Consequatur, vitae.
          </p>
          <p>
            Nostrum asperiores corrupti expedita a voluptatem dignissimos
            temporibus tenetur ut amet unde consectetur sapiente, accusantium
            itaque tempora, animi debitis totam quia recusandae quam aut?
            Dolorem, sit deleniti! Temporibus, exercitationem deserunt?
          </p>
        </div>

        <div
          className="razorpay-embed-btn"
          data-url="https://pages.razorpay.com/pl_GRihTTyVe4ROqd/view"
          data-text="Donate Now"
          data-color="#528FF0"
          data-size="large"
          style={{
            textAlign: "center",
          }}
        ></div>
      </div>
    </>
  );
};

export default App;
