import { Link } from "react-router-dom";
import logoImg from "./assets/img/logo.png";

var App = () => {
  var searchActive = (route) => {
    return window.location.pathname == route ? "active" : null;
  };
  return (
    <>
      <div className="nav">
        <img src={logoImg} />
        <ul>
          <li className={searchActive("/")}>
            <Link to="/">HOME</Link>
          </li>
          <li className={searchActive("/about")}>
            <Link to="/about">ABOUT</Link>
          </li>
          <li className={searchActive("/donate")}>
            <Link to="/donate">DONATE NOW</Link>
          </li>
          <li className={searchActive("/contact")}>
            <Link to="/contact">CONTACT US</Link>
          </li>
        </ul>
      </div>
    </>
  );
};

export default App;
