import { useEffect } from "react";
import donateImg from "./assets/img/donate.jpg";

const App = () => {
  useEffect(() => {
    document.title = "Donate | YM";
  });
  useEffect(() => {
    var d = document;
    var x = !d.getElementById("razorpay-embed-btn-js");
    if (x) {
      var s = d.createElement("script");
      s.defer = !0;
      s.id = "razorpay-embed-btn-js";
      s.src = "https://cdn.razorpay.com/static/embed_btn/bundle.js";
      d.body.appendChild(s);
    } else {
      var rzp = window["__rzp__"];
      rzp && rzp.init && rzp.init();
    }
  });
  return (
    <>
      <img src={donateImg} style={{ width: "100%", marginBottom: "-10px" }} />
      <div
        style={{
          position: "absolute",
          zIndex: "50",
          top: "60px",
          right: "150px",
          width: "400px",
          textAlign: "center",
          fontSize: "50px",
          fontFamily: "Roboto Slab",
          color: "#333",
        }}
      >
        Helping Hands
      </div>
      <div
        style={{
          position: "absolute",
          zIndex: "50",
          top: "150px",
          right: "150px",
          width: "400px",
          fontSize: "20px",
          textAlign: "center",
          color: "#024850",
          padding: "20px",
          backgroundColor: "rgba(100,100,100,0.3)",
          borderRadius: "20px",
        }}
      >
        Lorem ipsum dolor sit amet consectetur adipisicing elit. Asperiores
        maxime architecto voluptas. Soluta magnam recusandae doloremque
        excepturi doloribus odio quo quaerat, officia ducimus optio omnis, dicta
        error obcaecati quod dolorem.
      </div>
      <div
        className="razorpay-embed-btn"
        data-url="https://pages.razorpay.com/pl_GRihTTyVe4ROqd/view"
        data-text="Donate Now"
        data-color="#528FF0"
        data-size="large"
        style={{
          position: "absolute",
          zIndex: "50",
          top: "400px",
          right: "200px",
        }}
      ></div>
    </>
  );
};

export default App;
