import { useEffect } from "react";
import reactLogo from "./assets/img/logo512.png";

const App = () => {
  useEffect(() => {
    document.title = "About | YM";
  });
  return (
    <>
      <div
        style={{
          padding: "40px 80px",
          backgroundColor: "#222",
          width: "100%",
          height: "80vh",
          color: "#ccc",
        }}
      >
        <img
          src={reactLogo}
          style={{ width: "150px", margin: "auto", display: "block" }}
        />
        <div
          style={{
            textAlign: "center",
            width: "100%",
            fontSize: "25px",
            fontWeight: "600",
          }}
        >
          A ReactJS Based Website
        </div>
        <br></br>
        The website aims for the purpose of demonstration of Payment Gateway. It
        is capable of:
        <br />
        <ul>
          <li>Accepting donation amounts</li>
          <li>
            Sending confirmation receipt to the donor through email instantly
          </li>
          <li>Notifying the amount of donation to the organisation</li>
        </ul>
        The payment gateway used in RazorPay.
      </div>
    </>
  );
};

export default App;
