import { useEffect } from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faPhoneAlt,
  faEnvelope,
  faMapMarkerAlt,
} from "@fortawesome/free-solid-svg-icons";
import yashImg from "./assets/img/yash.jpg";

const App = () => {
  useEffect(() => {
    document.title = "Contact Us | YM";
  });
  return (
    <>
      <div
        style={{
          backgroundColor: "#FFD4A1",
          display: "block",
          width: "1000px",
          height: "1000px",
          borderRadius: "90px",
          transform: "rotateZ(-60deg)",
          position: "absolute",
          top: "-550px",
          right: "-400px",
        }}
      />
      <img
        src={yashImg}
        style={{
          width: "200px",
          borderRadius: "1000px",
          position: "absolute",
          right: "500px",
          top: "100px",
          boxShadow: "0px 0px 30px 4px #A78555",
        }}
      />
      <div
        style={{
          position: "absolute",
          top: "150px",
          left: "50px",
          fontSize: "50px",
          fontWeight: "600",
          color: "#754D14",
        }}
      >
        I'm Yash Mittal
      </div>
      <div
        style={{
          position: "absolute",
          top: "250px",
          left: "80px",
          fontSize: "20px",
          fontWeight: "600",
          color: "#444",
          lineHeight: "34px",
        }}
      >
        <FontAwesomeIcon icon={faPhoneAlt} /> +91 75794 53101
        <br />
        <FontAwesomeIcon icon={faEnvelope} /> 23.yashm@gmail.com
        <br />
        <FontAwesomeIcon icon={faMapMarkerAlt} /> IIT Ropar, India
      </div>
    </>
  );
};

export default App;
